body{
  margin: 0px;
  padding: 0;
  background: rgb(39, 39, 39);
  overflow-x: hidden;
  user-select: none;
}
::-webkit-scrollbar{
  width: 1px;
}
.taskmanager{
  width: 100vw;
  height: 50px;
}
.signupbutton{
  height: 20px;
  width: 50px;
  border: 2px solid white;
  border-radius: 10px;
  padding: 10px;
  position: absolute;
  top: 20px;
  right: 20px;
}
.menubutton{
  height: 30px;
  aspect-ratio: 1;
  position: absolute;
  top: 20px;
  left: 20px;
  background: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.taskmanager h2{
  color: skyblue;
  text-align: center;

}
.taskarea{
  display: flex;
}
.taskmanagerleft{
  text-align: center;
  overflow-y: scroll;
  height:calc( 100vh - 90px);
  margin-left: 8px;
  width: 40vw;
  border: 1px solid;
  padding: 0px 10px 60px 10px ;
  box-sizing: border-box;
  position: relative;
  color: white;
  /* transform: translateX(-40vw); */
}
#refresh{
  height: 30px;
  border-radius: 15px;
  position: absolute;
  top: 17px;
  right: 10px;
  background: rgb(0, 136, 255);
  border: 2px solid white;
  color: white;
}
.addtask{
  height: 50px;
  border-radius: 25px;
  width: calc(100% - 20px);
  position: absolute;
  box-sizing: border-box;
  background: green;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.taskmanagerright{
  width: 70vw;
  padding: 10px;
  color: white;
}

/* tasklist */
.tasklisttask{
  border: 2px solid white;
  display: flex;
  align-items: center;
  height: 50px;
  border-radius: 25px;
  padding-left: 10px;
  width: 100%;
  margin-bottom: 10px;
  justify-content: space-evenly;
  box-sizing: border-box;
  overflow: hidden;
}
.tasklisttask h3{
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
}
.tasklisttask button{
  border: none;
  min-height: 48px;
  min-width: 60px;
  border-radius: 25px 0px 0px 25px;
}
.tasklisttask button:nth-child(3){
  border-radius: 0px 25px 25px 0px;
  border-left: 1px solid black;
}
.tasklisttask button:nth-child(3):hover{
  background: red;
  color: white;
  width: 60px;
}

/* showtask */
.secondarydetails{
  display: flex;
  justify-content: space-around;
}
.complete{
  position: absolute;
  bottom: 20px;
  right: 10px;
  background: green;
  padding: 10px;
  border-radius:20px ;
}
/* create task */
.createtask{
  display: grid;
}
.createtask button{
  max-width: 200px;
  min-width: 200px;
  height: 30px;
  border-radius: 15px;
  justify-self: center;
  border: none;
}
.createtask input{
  min-width:100%;
  background: transparent;color: white;
  border: 2px solid white;
  height: 30px;
  justify-self: center;
  margin-top: 10px;
}
.createtask button:hover{
  background: green;
  color: white;
}


/*  */

@media screen and (width<800px) {
  .menubutton{
    display: flex;
  }
  .taskmanagerleft{
    transform: translateX(-40vw);
    transition: 0.3s;
    width: 0;
    
  }
  .taskmanagerright{
    width: 100vw;
    transition: 0.3s;
  }
}
@media screen and (width<550px) {
  .menubutton{
    display: flex;
  }
  .secondarydetails{
    display: flex;
    flex-direction: column;
  }
}